.header
{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header_logo
{
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}
.header_search
{
    display: flex;
    flex: 1;
    align-items: center; 
    border-radius: 24px;
}
.header_searchInput
{
    height: 12px;
    padding: 10px;
    border: none;
    width: 85%;
    margin-left: 40px;
}
.header_searchIcon
{
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
}
.header__lineOne
{
    font-size: 12px;
}
.header__lineTwo
{
    font-size: 14px; 
    font-weight: 700;
}
.header_basket
{
    display: flex;
    align-items: center;
    color: white;
}
.header_basketCount
{
    margin-left: 10px;
    margin-right: 10px;
}
.header_nav
{
    display: flex;
    justify-content: space-evenly;
}
.header__category
{
    display: flex; 
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}