.product
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
    transition: all 0.3s ease;
}
.product:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); 
    transform: translateY(-5px); 
  }
.product_rating
{
    display: flex;
}
.product > img
{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product > button
{
    background: #f1c047;
    border: 2px solid;
    margin-top: 10px;
    height: 30px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    border-radius: 8px;
    transition: all 0.3s ease;
}
.product > button:hover 
{
    background: #f8cb78; 
    cursor: pointer;
}
.product > button:active 
{
    background: #a88734;
    border-color: #846a29 #a88734 #9c7e31;
    transform: translateY(2px);
}
.product_price
{
    margin-top: 5px;
}
.product_info
{
    height: 100px;
    margin-bottom: 15px;
}